export class PostIt {

  constructor(
    public titel: string,
    public kleur: string,
    public urls: string[]
  ) { }

  public isGroen(): boolean {
    return this.kleur === 'groen';
  }

  public hasMultipleUrls(): boolean {
    return this.urls.length > 1;
  }

  public hasUrl(): boolean {
    return this.urls.length !== 0;
  }

}
